import { loadModules } from 'esri-loader';
import config from '../configs.json';


let sources = [];
let searchWidget;

const dataSource = () => {
    sources.push({
        layer: config.data.investmentFLayer,
        searchFields: config.searchWidget.sources[0].searchFields,
        displayField: config.searchWidget.sources[0].displayField,
        exactMatch: false,
        outFields: config.searchWidget.sources[0].outFields,
        name: config.searchWidget.sources[0].name,
        placeholder: config.searchWidget.sources[0].placeholder,
        suggestionsEnabled: true,
      });
      console.log(sources);
    return sources;
}

const addSearchWidget = async(mapView, position) => {
    const [Search, FeatureLayer] = await loadModules([
        "esri/widgets/Search",
        "esri/layers/FeatureLayer"
      ]);
    // let parent = document.body;
    // let search = document.createElement('div');
    // search.id = "search";
    // parent.appendChild(search);
    let layer = new FeatureLayer({ url: config.layers[0].url }); // config.layers[0].url
    searchWidget = new Search({
        view: mapView,
      //  allPlaceholder: this.data.resources.search,
     //   container: document.getElementById("search"),
        includeDefaultSources: false,
        locationEnabled: false,
        sources:[{
            layer: layer,
            searchFields: config.searchWidget.sources[0].searchFields,
            displayField: config.searchWidget.sources[0].displayField,
            exactMatch: false,
            outFields: "*",
            name: config.searchWidget.sources[0].name,
            placeholder: config.searchWidget.sources[0].placeholder,
            //suggestionsEnabled: true,
        }],
    })
    mapView.ui.add(searchWidget, {position: position});
    addSearchCompletedEvent();
}

const addSearchCompletedEvent = () => {
    searchWidget.on("search-complete", function (event) {
        if(event.results[0].results.length > 0){
            let results = event.results[0].results.map(item => item.feature);
            console.log(results);
        }else{
            alert('لا توجد نتائج للبحث');
        }
    });
}

export {
    addSearchWidget
}